// Form
//

form {
    margin-bottom: $generic-margin-bottom;
}

input {
    &[type="date"],
    &[type="datetime"],
    &[type="datetime-local"],
    &[type="email"],
    &[type="month"],
    &[type="number"],
    &[type="password"],
    &[type="search"],
    &[type="tel"],
    &[type="text"],
    &[type="url"],
    &[type="week"] {
        appearance: none; // remove awkward defaults on ios
        @include input;
        @include input-size; 
        @include input-width;
        @include input-focus;
    }
}

input {
    &[type="date"],
    &[type="datetime-local"],
    &[type="month"],
    &[type="week"] {
        appearance: listbox; // see https://bugs.webkit.org/show_bug.cgi?id=139848

        // removes the extra height in date controls in webkit 
        &::-webkit-datetime-edit-fields-wrapper {
            padding: 0;
            margin: 0;
        }

        // the date calendar controls are only available in webkit browsers
        // and should not be relied upon globally
        @if $remove-input-date-controls {
            &::-webkit-clear-button,
            &::-webkit-inner-spin-button,
            &::-webkit-calendar-picker-indicator {
                display: none;
            }
        } 
    }
}

textarea {
    appearance: none; // remove awkward defaults on ios
    @include input;
    @include input-width;
    @include input-focus;
}

select {
    @include input;
    @include input-width;
    @include input-focus;

    &:not([multiple]) {
        @include input-size($fix-height: true); 
    }
}

input::placeholder,
textarea::placeholder {
    color: $input-placeholder-color;
    opacity: 1;
}

label,
legend {
    display: block;
    margin-bottom: .5rem;
    font-weight: $label-font-weight;
}

fieldset {
    padding: 0;
    border-width: 0;
    margin-bottom: $input-margin-bottom;
}

// only set some simple margin as components will handle the style better
// https://github.com/AccentDesign/karma-css-components
input {
    &[type="checkbox"],
    &[type="radio"] {
        margin-right: 1rem;
    }
}
